import React, { useEffect } from 'react';
import { useInView } from 'react-intersection-observer';
import querystring from 'query-string';

function PagnationWrapperHandler({ children, page }) {
    const { ref, inView } = useInView({
        /* Optional options */
        threshold:  0,
        rootMargin: '0px 0px -50%'
    });

    // useEffect(() => {
    //     const { page: currentPage = 1 } = querystring.parse(window.location.search);
    //     console.log(page, currentPage);
    //     if (inView && Number(currentPage) !== page) {
    //         var pageUrl = page === 1 ? '/' : '?page=' + page;
    //         window.history.pushState('', '', pageUrl);
    //     }
    // }, [inView]);

    const { page: currentPage = 1 } = querystring.parse(window.location.search);
    console.log(page, currentPage);
    if (inView && Number(currentPage) !== page) {
        var pageUrl = page === 1 ? '/' : '?page=' + page;
        window.history.pushState('', '', pageUrl);
    }

    return <div ref={ref}>{children}</div>;
}

export default PagnationWrapperHandler;
